<template lang="pug">
  .foe__calc
    .flex.main
      .input
        label Стоимость уровня
        input(type="number" v-model="price" v-on:click="this.select();")
        img.icon(src="../assets/so.png", alt="")
      .input
        label Коэффициент
        input(type="number" v-model="rate" v-on:click="this.select();")
        img.icon(src="../assets/perc.png", alt="")
    .flex.th
      .id #
      .price Необходимо
      .summ Цена {{ rate }}
    .flex.td(v-for="(item, index) in places")
      .id {{index + 1}}
      .price 
        input(type="number" v-model="item.value" v-on:click="this.select();")
        img.icon(src="../assets/so.png", alt="")
      .summ 
        span.text {{ Math.round(item.value * rate) }}
        img.icon(src="../assets/so.png", alt="")
    .garant 
      .label Нужно внести 
      .summ 
        span.text {{garant}}
        img.icon(src="../assets/so.png", alt="")
</template>

<script>
  export default {
    name: 'WidgetCalc',
    data: function() {
      return {
        rate: 1.9,
        price: 0,
        places: [
          { value: 0 },
          { value: 0 },
          { value: 0 },
          { value: 0 },
          { value: 0 },
        ],
      };
    },

    computed: {
      garant: function() {
        let garantValue = 0;
        let reserved = 0;
        for (let i = 0; i < this.places.length; i++) {
          const item = this.places[i].value;
          reserved = reserved + Math.round(item * this.rate);
        }

        garantValue = this.price - reserved;
        return garantValue;
      },
    },
  };
</script>
